.portfolio {
    padding: 50px 0;
    background-color: #f9f9f9;
}

.portfolio-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
    text-align: center;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 40px; /* Increase space between cards */
}

.portfolio-title {
    font-size: 3rem;
    color: #1a1a1a;
    margin-bottom: 60px;
    width: 100%; /* Ensure title spans full width */
    text-align: center;
}

.portfolio-card {
    background-color: #2a2a2a;
    border-radius: 15px;
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s;
    width: 400px; /* Increase card width */
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow: hidden;
    height: 100%; /* Ensure consistent height */
}

.portfolio-card:hover {
    transform: translateY(-10px);
}

.portfolio-card-link {
    text-decoration: none;
    color: inherit;
}

.portfolio-card-content {
    padding: 25px;
    display: flex;
    flex-direction: column;
    height: 100%; /* Ensures the card stretches */
    justify-content: space-between; /* Push content to top and bottom */
}

.portfolio-image {
    width: 100%;
    height: 250px; /* Increase the image height */
    object-fit: contain; /* Ensure the entire image is visible */
    border-radius: 15px 15px 0 0;
    background-color: #ccc; /* Placeholder background */
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    color: #666;
}

.portfolio-project-title {
    font-size: 1.75rem;
    color: #00bfff;
    margin-top: 15px;
}

.portfolio-project-date {
    font-size: 1.1rem;
    color: #6c757d;
}

.portfolio-project-description {
    font-size: 1.15rem;
    color: #cccccc;
    margin: 15px 0;
}

.portfolio-tags {
    display: flex;
    justify-content: center;
    flex-wrap: wrap; /* Ensure tags wrap within the card */
    margin-top: 15px;
    padding-bottom: 15px; /* Adds padding to ensure space for tags */
}

.tag {
    background-color: #007bff;
    color: white;
    padding: 7px 15px;
    border-radius: 25px;
    margin: 8px;
    font-size: 1rem;
    display: inline-block;
}
