.header {
    display: flex;
    justify-content: space-between;
    padding: 20px;
    background-color: #ecf0f1;
}

.logo {
    font-size: 24px;
    font-weight: bold;
}

nav ul {
    list-style: none;
    display: flex;
    gap: 15px;
}

nav a {
    font-weight: bold;
    color: #2980b9;
}
