.experience {
    padding: 50px 0;
    background-color: #f9f9f9;
}

.experience-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

.experience-title {
    font-size: 2.5rem;
    color: #1a1a1a;
    margin-bottom: 30px;
    text-align: center;
}

.experience-item {
    margin-bottom: 40px;
}

.experience-position {
    font-size: 1.5rem;
    color: #007bff;
    margin-bottom: 10px;
    font-weight: bold;
}

.experience-date {
    font-size: 1rem;
    color: #6c757d;
    margin-bottom: 10px;
    display: block;
}

.experience-description {
    font-size: 1.1rem;
    color: #333;
    line-height: 1.6;
}
